.project-preview-image {
  height: 200px;

}

.navbar-bg-purple {
  background-color: #603983;
}

.navbar-text-inactive {
  color: rgb(236, 228, 255) !important;
}
.navbar-text-active {
  color: rgb(250, 244, 255) !important;
  font-weight: 600;
}

.sidebar-links a {
  cursor: pointer;
  display: block;
  color: rgba(0, 0, 0, .65);
}

.preview-image {
  background-color: #868e96;
  height: 200px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: auto 100%;
  position: relative;
}

@media (min-width: 768px) {
  .sidebar {
    height: calc(100vh - 4rem);
    position: sticky !important;
    top: 4rem;
    border-right: 1px solid #dee2e6!important;
  }
}
@media (max-width: 768px) {
  .sidebar {
    border-bottom: 1px solid #dee2e6 !important;
  }
}

@media (max-width: 991.98px) {
  .navbar-nav-scroll {
    max-width: 100%;
    height: 2.5rem;
    margin-top: .25rem;
    overflow: hidden;
    white-space: nowrap;
  }
}

.navbar-nav-scroll > ul {
  overflow: auto;
}

